#button1 {
  display: flex;
  align-items: center;
  background: #213744;
  border-radius: 6px;
  border: none;
  font-size: 1.2rem;
  width: 100%;
  text-align: left;
  color: #ffffff;
  font-weight: bold;
  margin-top: 0.5rem;
  padding: 14px;
}

#button1.active {
  background-color: #213744;
  color: #ffffff;
  border: #213744 solid 1px;
}

#button1:hover {
  background-color: #F3F7FF;
  color: #213744;
  transition: all 0.5s;
  border: solid 1px #F3F7FF;
}

.title-wrapper {
  display: block;
  position: relative;
  width: 100%;
}

.minus {
  content: url(/vivekanand111/status-reports/static/media/line.747a41c6.svg);
  width: 24px;
  height: 24px;
  transform: rotate(180deg);
  transition-timing-function: ease-in;
  transition: all 1s;
  position: absolute;
  right: 0;
  top: -6px;
}

.plus {
  content: url(/vivekanand111/status-reports/static/media/plus.4827eb6b.svg);
  width: 24px;
  height: 24px;
  transform: rotate(90deg);
  transition-timing-function: ease-in;
  transition: all 1s;
  position: absolute;
  right: 0;
  top: -5px;
}

.panel-close { 
  background: #e0e0e0;
  box-sizing: border-box;
  padding: 0.5rem 1rem 0 1rem;
  opacity:0;
  width:100%;
  height:0;
  overflow: hidden;
  transition-timing-function: ease-out;
  transition: all 1s;
}

.panel-open {
  overflow: hidden;
  background: #F3F7FF;
  padding: 1rem;
  margin-bottom: 1rem;
  box-sizing: border-box;
  border-radius: 1rem;
  opacity:1;
  width:100%;
  height: auto;
  transition-timing-function: ease-in;
  transition: all 1s;
}
